.nz-target-mergeTarget {
  height: 4em;
  width: 4em;
  border: 2px solid var(--buic-background-5);
  border-radius: var(--iui-border-radius-1);
  box-sizing: border-box;
  background-color: var(--buic-background-4);
  transition: transform var(--iui-duration-1) ease-in;
  pointer-events: all;
  display: flex;
  box-shadow: var(--iui-shadow-3);
  z-index: 5000;
  animation: nz-grow var(--iui-duration-1) ease-in-out, nz-pulse 3s ease-in-out 2s infinite;
}
@media (prefers-reduced-motion: no-preference) {
  @keyframes nz-pulse {
    from {
      transform: scale3d(1, 1, 1);
    }
    50% {
      transform: scale3d(1.1, 1.1, 1.1);
    }
    to {
      transform: scale3d(1, 1, 1);
    }
  }
  @keyframes nz-grow {
    from {
      transform: scale3d(0, 0, 0);
    }
    to {
      transform: scale3d(1, 1, 1);
    }
  }
}
.nz-target-mergeTarget.nz-hidden {
  display: none;
}
.nz-target-mergeTarget.nz-targeted {
  transform: scale(1.2);
  background-color: rgb(140, 210, 255);
  box-shadow: var(--iui-shadow-4);
  animation-iteration-count: 0, 0;
}