.nz-footer-messageCenter-message {
  min-height: 48px;
  box-sizing: border-box;
  margin-left: 8px;
  margin-right: 8px;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 8px;
  padding-bottom: 8px;
  display: grid;
  grid-gap: 12px;
  grid-auto-flow: column;
  align-items: center;
  grid-template-columns: auto 1fr;
  line-height: 16px;
  color: var(--iui-color-text);
  font-size: 14px;
}
.nz-footer-messageCenter-message:not(:first-child) {
  border-top: 1px solid var(--buic-background-4);
}
.nz-footer-messageCenter-message > .nz-icon {
  min-width: 20px;
}
.nz-footer-messageCenter-message > .nz-content {
  overflow: hidden;
  overflow-wrap: break-word;
}