.components-color-swatch {
  font-size: var(--iui-font-size-1);
  height: 1.2em;
  width: 1.2em;
  border-width: 1px;
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  border-color: var(--iui-color-border-subtle);
}
.components-color-swatch.round {
  border-radius: 50%;
}
.components-color-swatch[disabled] {
  opacity: 0.2;
}