.property-grid-react-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.property-grid-react-container .property-grid-react-data {
  overflow: scroll;
  height: calc(100% - var(--iui-size-l) * 2);
}
.property-grid-react-container .property-grid-header-title {
  width: 100%;
  overflow: hidden;
}
.property-grid-react-container .property-grid-header-title .property-grid-header-title-text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.property-grid-react-panel-label-and-class {
  flex: 1;
  overflow: hidden;
}
.property-grid-react-panel-label-and-class > * {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}