.solar-slider.solar-timeline > .iui-slider-container {
  height: 28px;
}

.solar-timeline-wrapper {
  box-sizing: border-box;
  background: var(--iui-color-background);
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 8px 16px;
  color: var(--iui-color-text);
  font-size: var(--iui-font-size-1);
}
.solar-timeline-wrapper > .header {
  display: flex;
  align-items: center;
  position: relative;
}
.solar-timeline-wrapper > .header > .play-button {
  width: 26px;
  height: 26px;
  background: inherit;
  border: none;
  font-size: 20px;
}
.solar-timeline-wrapper > .header > .shadow-settings-button {
  display: flex;
  align-items: center;
  color: var(--iui-color-icon);
  width: 26px;
  height: 26px;
  background: inherit;
  border: none;
  font-size: 16px;
  margin-left: 6px;
  cursor: pointer;
}
.solar-timeline-wrapper > .header > .shadow-settings-button:hover {
  color: var(--iui-color-icon-hover);
}
.solar-timeline-wrapper > .header > .shadow-settings-button:focus-visible {
  outline: 1px solid var(--iui-color-border-accent);
  outline-offset: -1px;
}
.solar-timeline-wrapper > .header > .current-date {
  min-width: 175px;
  margin-left: 8px;
  margin-right: 4px;
  background: inherit;
  padding: 3px 4px;
  border: none;
  display: flex;
  align-items: center;
  color: var(--iui-color-text);
  font-size: var(--iui-font-size-1);
  cursor: pointer;
}
.solar-timeline-wrapper > .header > .current-date:focus-visible {
  outline: 1px solid var(--iui-color-border-accent);
  outline-offset: -1px;
}
.solar-timeline-wrapper > .header > .current-date > span:nth-child(2) {
  margin: 0 6px;
}
.solar-timeline-wrapper > .header > .current-date > .icon-calendar {
  flex-grow: 1;
  margin-left: auto;
}
.solar-timeline-wrapper > .header > .speed-container {
  display: flex;
  align-items: center;
  margin-left: auto;
  cursor: pointer;
}
.solar-timeline-wrapper > .header > .speed-container > .speed {
  margin-left: 12px;
  width: 0;
  opacity: 0;
  transition: all 0.2s linear;
  margin-right: 10px;
}
.solar-timeline-wrapper > .header > .loop-playback {
  color: var(--iui-color-icon-accent);
  font-size: 16px;
  cursor: pointer;
}
.solar-timeline-wrapper > .header > .loop-playback:focus {
  outline: none;
}
.solar-timeline-wrapper > .header > .no-loop-playback {
  color: var(--iui-color-icon);
  font-size: 16px;
  cursor: pointer;
}
.solar-timeline-wrapper > .header > .no-loop-playback:hover {
  color: var(--iui-color-icon-hover);
}
.solar-timeline-wrapper > .header > .no-loop-playback:focus {
  outline: none;
}
.solar-timeline-wrapper .solar-slider {
  position: relative;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  flex: 1;
}
.solar-timeline-wrapper .solar-slider .rail-outer {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 20px;
  cursor: pointer;
}
.solar-timeline-wrapper .solar-slider .rail-inner,
.solar-timeline-wrapper .solar-slider .rail-solar {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 4px;
  pointer-events: none;
}
.solar-timeline-wrapper .solar-slider .rail-inner {
  width: 100%;
  background: var(--iui-color-background-backdrop);
}
.solar-timeline-wrapper .solar-slider .rail-solar {
  background: var(--iui-color-background-accent);
  z-index: 1;
}
.solar-timeline-wrapper .solar-slider .rail-tooltip {
  position: absolute;
  margin-left: -11px;
  top: 0;
  transform: translateY(-50%);
  z-index: 1;
}
.solar-timeline-wrapper .solar-slider .solar-handle {
  position: absolute;
  top: 50%;
  z-index: 4;
  width: 38px;
  height: 20px;
  border-radius: var(--iui-border-radius-1);
  border: 1px solid var(--buic-background-5);
  background-image: linear-gradient(to bottom, var(--buic-background-3), var(--buic-background-3)), linear-gradient(to bottom, var(--buic-background-1), var(--buic-background-1));
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.solar-timeline-wrapper .solar-slider .solar-handle > div {
  width: 1px;
  height: 10px;
  opacity: 0.25;
  margin: 0 1px;
  background-color: var(--iui-color-text);
}
.solar-timeline-wrapper .solar-slider .tick-mark {
  position: absolute;
  bottom: calc(4px + 15px);
  width: 1px;
  height: 5px;
  background: rgb(200, 200, 200);
}
.solar-timeline-wrapper .solar-slider .tick-label {
  position: absolute;
  bottom: 4px;
  font-size: 10px;
  text-align: center;
}
.solar-timeline-wrapper .solar-slider .sunrise,
.solar-timeline-wrapper .solar-slider .sunset {
  font-size: 22px;
  cursor: default;
}
.solar-timeline-wrapper .solar-slider .sunset {
  margin: 0px 16px 2px 24px;
}
.solar-timeline-wrapper .solar-slider .sunrise {
  color: var(--iui-color-icon-warning);
  margin: 0 24px 0 8px;
}

.solar-timeline-wrapper > .header > .speed-container:hover > .speed {
  opacity: 1;
  width: 80px;
}

.solar-timeline-wrapper.expanded .header .expanded-icon {
  transform: rotate(180deg);
}

.solar-timeline-wrapper.expanded > .header > .current-date {
  margin-left: 70px;
}

.solar-timeline-wrapper .solar-timeline-expanded {
  display: none;
  margin-bottom: 8px;
}

.solar-timeline-wrapper .solar-timeline {
  display: flex;
}

.solar-timeline-wrapper.expanded {
  height: 100px;
}

.shadow-settings-color-top {
  display: inline;
}

.shadow-settings-color-bottom {
  display: flex;
  flex-direction: row;
}

.shadow-settings-popup-container {
  width: 220px;
  padding: 10px 10px 0px;
  display: flex;
  flex-direction: column;
}
.shadow-settings-popup-container .shadow-settings-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 6px;
}
.shadow-settings-popup-container .shadow-settings-color {
  display: flex;
  flex-direction: column;
}
.shadow-settings-popup-container .shadow-settings-color .shadow-settings-color-top {
  height: 150px;
  margin: 1em;
}
.shadow-settings-popup-container .shadow-settings-color .shadow-settings-color-bottom {
  width: 100%;
  height: 32px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.shadow-settings-popup-container .shadow-settings-color .shadow-settings-color-bottom .shadow-settings-color-bottom-left {
  width: calc(100% - 2em - 32px);
  height: 100%;
  margin-left: 1em;
  margin-right: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shadow-settings-popup-container .shadow-settings-color .shadow-settings-color-bottom .shadow-settings-color-bottom-right {
  width: 32px;
  height: 100%;
}
.shadow-settings-popup-container .shadow-settings-color-presets {
  width: 100%;
  height: 24px;
  padding-top: 10px;
  margin-bottom: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 2px;
  border-top: 1px solid var(--iui-color-border-subtle);
}

.sunrise-tip {
  position: absolute;
  top: -8px;
  display: inline-block;
  border-bottom: 1px dotted rgba(0, 0, 0, var(--iui-opacity-3));
  font-size: 14px;
}

.solar-tooltip-text {
  width: 100px;
  background-color: rgba(0, 0, 0, var(--iui-opacity-3));
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 8px 0;
  position: absolute;
  z-index: 1;
  bottom: 150%;
  left: 50%;
  margin-left: -60px;
}
.solar-tooltip-text:after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, var(--iui-opacity-3)) transparent transparent transparent;
}