.uicore-centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.uicore-full-height {
  height: 100%;
}

.uicore-full-width {
  width: 100%;
}

.uicore-full-size {
  height: 100%;
  width: 100%;
}

.uicore-fill-centered {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.uicore-scrollview {
  height: 100%;
  width: 100%;
  overflow-y: auto;
}
@supports (-webkit-overflow-scrolling: touch) {
  .uicore-scrollview {
    overflow-y: scroll; /* overflow-y has to be scroll, not auto */
    -webkit-overflow-scrolling: touch;
  }
}
.uicore-scrollview::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}
.uicore-scrollview::-webkit-scrollbar-thumb {
  border-radius: 12px;
  background-color: var(--buic-background-scrollbar);
  background-clip: content-box;
  border-style: solid;
  border-color: transparent;
  border-width: 2px;
}
.uicore-scrollview::-webkit-scrollbar-thumb:hover {
  background-color: var(--buic-background-scrollbar-hover);
}
.uicore-scrollview::-webkit-scrollbar-corner {
  background-color: transparent;
}
.uicore-scrollview::-webkit-scrollbar-track-piece:corner-present {
  margin-bottom: -4px;
  margin-right: -4px;
}

.uicore-flex-wrap-container {
  display: flex;
  flex-wrap: wrap;
}

