}
.uicore-expandable-blocks-list > .iui-expanded + div.iui-expanded > .iui-header {
  border-top-color: var(--iui-color-border-accent);
}
.uicore-expandable-blocks-list > .iui-expanded + .iui-expanded {
  margin-top: 0;
}

