.property-grid-react-panel-header {
  display: grid;
  grid-template-areas: "title tools";
}
.property-grid-react-panel-header .property-grid-react-header-back-button {
  margin-right: var(--iui-size-xs);
}
.property-grid-react-panel-header .header-title {
  display: flex;
  justify-content: flex-start;
  grid-area: title;
  padding-block: 5px;
  align-items: center;
  overflow: hidden;
}
.property-grid-react-panel-header .header-tools {
  grid-area: tools;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  overflow: visible;
}
.property-grid-react-panel-header .expandable-search-bar {
  overflow: hidden;
}
.property-grid-react-panel-header .expandable-search-bar.contracted {
  outline: none;
}
.property-grid-react-panel-header .expandable-search-bar.contracted::before {
  border: none;
}
.property-grid-react-panel-header.search-bar-expanded {
  display: grid;
  grid-template-areas: "tools" "title";
}
.property-grid-react-panel-header.search-bar-expanded .header-tools {
  overflow: hidden;
}