.filter-builder > .rule-group > .rule-group-content {
  border-left: 0;
  padding-left: 0;
}
.filter-builder > .rule-group > .rule-group-remove-action {
  padding-right: 0;
}

.rule-group {
  display: grid;
  grid-template-columns: min-content auto;
  padding: 11px 0;
}
.rule-group[data-isactive=true] > .rule-group-remove-action {
  visibility: visible;
}
.rule-group[data-isactive=true] > .rule-group-content {
  border-color: var(--iui-color-border-foreground-hover);
}
.rule-group[data-isactive=false] > .rule-group-content > .rule-group-actions .iui-button {
  color: var(--iui-color-icon);
}
.rule-group .rule-group-remove-action {
  padding: 3px var(--iui-size-s) 3px 0;
  visibility: hidden;
}
.rule-group .rule-group-content {
  padding-left: var(--iui-size-s);
  border-left: 1px solid;
  box-sizing: border-box;
  border-color: var(--iui-color-border-subtle);
}
.rule-group .rule-group-content .rule-group-operator {
  padding: 3px 0;
  width: max-content;
}
.rule-group .rule-group-content .rule-group-actions {
  padding: 3px 0;
  display: grid;
  grid-template-columns: min-content min-content;
  gap: 8px;
}

.rule-group-items > .rule-group {
  padding-bottom: 22px;
}
.rule-group-items > .rule-group:last-child {
  padding-bottom: 11px;
}