/** Toolbar Opacity */
.uifw-statusFields-selectionScope {
  overflow: hidden;
  padding-right: 0px;
}
.uifw-statusFields-selectionScope > .uifw-statusFields-selectionScope-label {
  display: inline;
  padding-right: 4px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
@media (max-width: 799px) {
  .uifw-statusFields-selectionScope > .uifw-statusFields-selectionScope-label {
    display: none;
    padding-right: 0px;
  }
}
.uifw-statusFields-selectionScope > .uifw-statusFields-selectionScope-selector {
  min-width: 140px;
}
.uifw-statusFields-selectionScope > .uifw-statusFields-selectionScope-selector.iui-select[data-iui-size=small] {
  width: auto;
}
@media (max-width: 799px) {
  .uifw-statusFields-selectionScope > .uifw-statusFields-selectionScope-selector {
    font-size: var(--iui-font-size-0);
  }
}
@media (max-width: 599px) {
  .uifw-statusFields-selectionScope > .uifw-statusFields-selectionScope-selector {
    font-size: var(--iui-font-size-0)-1px;
  }
}

.uifw-statusFields-selectionScope-label {
  text-overflow: ellipsis;
  overflow: hidden;
}