.uifw-position-popup {
  border-color: var(--iui-color-border);
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  border-width: 1px;
  background-color: hsl(var(--iui-color-background-hsl)/var(--iui-opacity-2));
  position: fixed;
  height: fit-content;
  width: fit-content;
}
.uifw-position-popup .uifw-position-popup-content {
  padding: var(--iui-size-2xs);
}
.uifw-position-popup.uifw-no-border {
  border-width: 0;
}