body.nz-nwse-resize * {
  cursor: nwse-resize !important;
  user-select: none;
}
body.nz-nesw-resize * {
  cursor: nesw-resize !important;
  user-select: none;
}
body.nz-ew-resize * {
  cursor: ew-resize !important;
  user-select: none;
}
body.nz-ns-resize * {
  cursor: ns-resize !important;
  user-select: none;
}
body.nz-grabbing * {
  cursor: grabbing !important;
  user-select: none;
}

.nz-widgetPanels-cursorOverlay_cursor.nz-nwse-resize {
  cursor: nwse-resize;
}
.nz-widgetPanels-cursorOverlay_cursor.nz-nesw-resize {
  cursor: nesw-resize;
}
.nz-widgetPanels-cursorOverlay_cursor.nz-ew-resize {
  cursor: ew-resize;
}
.nz-widgetPanels-cursorOverlay_cursor.nz-ns-resize {
  cursor: ns-resize;
}
.nz-widgetPanels-cursorOverlay_cursor.nz-grabbing {
  cursor: grabbing;
}