.nz-base-ellipsis {
  display: flex;
  flex-direction: row;
}
.nz-base-ellipsis > .nz-dot {
  width: 0.25em;
  height: 0.25em;
  border-radius: 50%;
  background-color: currentColor;
}
.nz-base-ellipsis > .nz-dot:not(:first-child) {
  margin-left: 3px;
}