.nz-footer-messageCenter-indicator {
  height: 100%;
  color: var(--iui-color-text);
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  /* small devices */
  /* Medium devices */
}
@media (max-width: 599px) {
  .nz-footer-messageCenter-indicator {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media (max-width: 1099px) {
  .nz-footer-messageCenter-indicator {
    padding-left: 10px;
    padding-right: 10px;
  }
}
.nz-footer-messageCenter-indicator > .nz-indicator {
  height: 100%;
  display: flex;
  cursor: pointer;
  align-items: center;
}
.nz-footer-messageCenter-indicator > .nz-indicator > .nz-label {
  font-size: 14px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
@media (max-width: 799px) {
  .nz-footer-messageCenter-indicator > .nz-indicator > .nz-label {
    font-size: 12px;
  }
}
@media (max-width: 599px) {
  .nz-footer-messageCenter-indicator > .nz-indicator > .nz-label {
    display: none;
  }
}
.nz-footer-messageCenter-indicator > .nz-indicator > .nz-container {
  height: 100%;
  position: relative;
  display: grid;
  align-items: center;
  margin-left: 8px;
}
.nz-footer-messageCenter-indicator > .nz-indicator > .nz-container > .nz-balloon {
  background-color: var(--iui-color-icon);
  text-align: center;
  min-width: 10px;
  min-height: 10px;
  border-radius: 50%;
  padding: 3px;
  position: relative;
}
.nz-footer-messageCenter-indicator > .nz-indicator > .nz-container > .nz-balloon > .nz-arrow {
  position: absolute;
  width: 0;
  bottom: -3px;
  left: 0;
  display: block;
  border-width: 0;
  border-bottom-width: 10px;
  border-left-width: 10px;
  border-style: solid;
  border-color: transparent var(--iui-color-icon);
  transform: rotate(25deg);
}
.nz-footer-messageCenter-indicator > .nz-indicator > .nz-container > .nz-balloon > .nz-content {
  position: relative;
  color: var(--iui-color-background);
  line-height: 10px;
  font-size: 11px;
}
.nz-footer-messageCenter-indicator > .nz-indicator > .nz-container > .nz-target {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
}
.nz-footer-messageCenter-indicator > .nz-indicator:hover {
  color: var(--iui-color-text-accent);
}
.nz-footer-messageCenter-indicator > .nz-indicator:hover > .nz-container > .nz-balloon {
  background-color: var(--iui-color-text-accent);
}
.nz-footer-messageCenter-indicator > .nz-indicator:hover > .nz-container > .nz-balloon > .nz-arrow {
  border-color: transparent var(--iui-color-text-accent);
}
.nz-footer-messageCenter-indicator > .nz-indicator:focus {
  outline: none;
}