/** Widget Opacity */
/** Toolbar Opacity */
.nz-widget-tabBar {
  background: var(--iui-color-background-backdrop);
  display: flex;
  border-radius: 2px 2px 0 0;
  flex: 0 0 auto;
  position: relative;
  height: 2.1em;
}
.nz-widget-tabBar.nz-separator {
  border-bottom: 1px solid var(--iui-color-border);
}
.nz-widget-tabBar > .nz-handle {
  width: 100%;
  height: 100%;
  position: absolute;
  cursor: auto;
  user-select: none;
}
.preview-enableMaximizedFloatingWidget-maximized .nz-widget-tabBar > .nz-handle {
  pointer-events: none;
}
.nz-widget-tabBar > .nz-widget-tabBarButtons {
  display: flex;
  align-items: center;
  align-content: center;
  height: 100%;
}