.core-element-separator {
  touch-action: none;
  display: inline-block;
  border-style: none;
  padding: 0px;
  opacity: 0;
  z-index: 1;
}
.core-element-separator--vertical {
  width: 100%;
  cursor: row-resize;
  background: linear-gradient(to bottom, transparent calc(50% - 1px), var(--iui-color-icon-accent) 50%, transparent calc(50% + 1px));
}
.core-element-separator--horizontal {
  height: 100%;
  cursor: col-resize;
  background: linear-gradient(to right, transparent calc(50% - 1px), var(--iui-color-icon-accent) 50%, transparent calc(50% + 1px));
}
.core-element-separator.core-element-separator-group-hovered {
  -webkit-animation: fade-in 0.2s;
  -moz-animation: fade-in 0.2s;
  -ms-animation: fade-in 0.2s;
  -o-animation: fade-in 0.2s;
  animation: fade-in 0.2s;
  opacity: 1;
}
.core-element-separator.core-element-separator-group-unhovered {
  -webkit-animation: fade-out 0.2s;
  -moz-animation: fade-out 0.2s;
  -ms-animation: fade-out 0.2s;
  -o-animation: fade-out 0.2s;
  animation: fade-out 0.2s;
  opacity: 0;
}
.core-element-separator:focus {
  outline: none;
}