.nz-backstage-item {
  position: relative;
  display: flex;
  align-items: center;
  padding-bottom: 24px;
  padding-top: 24px;
  cursor: pointer;
  -webkit-transition: 0.2s ease;
  -moz-transition: 0.2s ease;
  -o-transition: 0.2s ease;
  transition: 0.2s ease;
}
@media (max-width: 1099px) {
  .nz-backstage-item {
    padding-bottom: 20px;
    padding-top: 20px;
  }
}
@media (max-width: 599px) {
  .nz-backstage-item {
    padding-bottom: 16px;
    padding-top: 16px;
  }
}
.nz-backstage-item:hover {
  background: var(--iui-color-background-hover);
  color: var(--iui-color-text-hover);
}
.nz-backstage-item.nz-active {
  background: var(--iui-color-background-accent-muted);
}
.nz-backstage-item.nz-active:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  margin-left: 2px;
  width: 2px;
  background: var(--iui-color-border-accent);
}
.nz-backstage-item.nz-disabled {
  color: var(--iui-color-text-disabled);
  pointer-events: none;
}
.nz-backstage-item div.nz-icon {
  display: flex;
  padding-left: 24px;
  padding-right: 16px;
}
.nz-backstage-item > div:last-child {
  display: flex;
  flex-direction: column;
  padding-right: var(--iui-size-s);
  overflow: hidden;
}
.nz-backstage-item > div:last-child > span {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.nz-backstage-item > div:last-child > span:nth-child(2) {
  margin-top: var(--iui-size-xs);
  font-size: var(--iui-font-size-1);
  color: var(--iui-color-text-muted);
}
@media (max-width: 1099px) {
  .nz-backstage-item > div:last-child > span:nth-child(2) {
    font-size: var(--iui-font-size-0);
  }
}
@media (max-width: 599px) {
  .nz-backstage-item > div:last-child > span:nth-child(2) {
    font-size: var(--iui-font-size-0)-1px;
  }
}
.nz-backstage-item div.nz-badge {
  position: absolute;
  top: 1px;
  left: 5px;
}
.nz-backstage-item.nz-safe-area-left {
  /* iOS 11 */
  padding-left: constant(safe-area-inset-left);
  /* iOS 11.2+ */
  padding-left: env(safe-area-inset-left);
}
.nz-backstage-item.nz-safe-area-left.nz-active:before {
  /* iOS 11 */
  left: constant(safe-area-inset-left);
  /* iOS 11.2+ */
  left: env(safe-area-inset-left);
}