.components-toolbar-button-item {
  box-sizing: border-box;
  background-origin: border-box;
  border-color: transparent;
  border-style: solid;
  border-radius: var(--iui-border-radius-1);
  cursor: pointer;
  transition: background-color var(--iui-duration-1) ease;
  position: relative;
  background: transparent;
  height: 100%;
  width: 100%;
  outline-style: solid;
  outline-width: 0;
}
.components-toolbar-button-item > .components-icon {
  font-size: 16px;
  width: 100%;
  height: 100%;
  display: grid;
  align-content: center;
  justify-content: center;
  color: var(--iui-color-icon);
  transition: color var(--iui-duration-1) ease;
}
.components-toolbar-button-item > .components-icon img {
  width: 16px;
  height: 16px;
}
.components-toolbar-button-item:not(.components-disabled) > .components-icon {
  filter: drop-shadow(0 1px 0 var(--buic-foreground-body-reverse));
}
.components-toolbar-button-item:focus-visible:not(.components-disabled):not(.components-active), .components-toolbar-button-item:hover:not(.components-disabled):not(.components-active), .components-toolbar-button-item:active:not(.components-disabled):not(.components-active) {
  color: var(--iui-color-icon-hover);
  background-color: var(--iui-color-background-transparent-hover);
}
.components-toolbar-button-item.components-active {
  color: var(--iui-color-icon-accent);
  background-color: var(--iui-color-background-accent-muted);
  border: solid 2px var(--iui-color-border-accent);
}
.components-toolbar-button-item.components-active:not(:active):not(.components-disabled) {
  transition-duration: 0s;
}
.components-toolbar-button-item.components-active:not(:active):not(.components-disabled) > .components-icon {
  color: var(--iui-color-icon-accent);
  transition-duration: var(--iui-duration-1);
  transition-timing-function: cubic-bezier(1, 0.1, 1, 0.5);
}
.components-toolbar-button-item.components-disabled {
  cursor: not-allowed;
}
.components-toolbar-button-item.components-disabled > .components-icon {
  color: var(--iui-color-icon-disabled);
}
.components-toolbar-button-item.components-disabled:hover {
  cursor: auto;
}
.components-toolbar-button-item.components-disabled:active {
  pointer-events: none;
}
.components-toolbar-button-item > .components-badge {
  display: none;
}

div.components-toolbar-items-container.components-toolbar-show-decorators .components-toolbar-button-item > .components-badge {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}