/** Toolbar Opacity */
.nz-widget-floatingTab {
  position: absolute;
  padding: 0 1em;
  display: flex;
  align-items: center;
  background: var(--buic-background-4);
  user-select: none;
  white-space: nowrap;
  height: 2.1em;
  border-radius: 2px;
  color: var(--iui-color-text-disabled);
  pointer-events: none;
  opacity: 0.6;
  gap: 6px;
  z-index: 23000;
}
.nz-widget-floatingTab.nz-hidden {
  display: none;
}
.nz-widget-floatingTab > span {
  max-width: 12em;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.9em;
}