.nz-target-tabTarget {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 5000;
}
.nz-target-tabTarget.nz-hidden {
  display: none;
}
.nz-target-tabTarget.nz-targeted {
  background-color: rgba(140, 210, 255, var(--iui-opacity-3));
}
.nz-target-tabTarget.nz-targeted > .nz-section.nz-target {
  background-color: rgba(140, 210, 255, var(--iui-opacity-3));
}