.rule {
  display: grid;
  grid-template-columns: min-content auto;
  padding: 3px 0;
}
.rule .rule-condition {
  display: grid;
  grid-template-columns: minmax(auto, 500px) 150px minmax(auto, 100%);
  grid-column-gap: var(--iui-size-m);
  width: calc(100% - var(--iui-size-s));
}
.rule .rule-condition .iui-input-container.iui-with-message .rule-value {
  grid-area: inputs;
}
.rule .rule-remove-action {
  padding-right: var(--iui-size-s);
  visibility: hidden;
}
.rule:focus-within .rule-remove-action, .rule:hover .rule-remove-action {
  visibility: visible;
}

.rule-group-items > .rule {
  padding-bottom: 14px;
}
.rule-group-items > .rule:first-child {
  padding-top: 14px;
}
.rule-group-items > .rule:only-child {
  padding-top: 3px;
}