/** Toolbar Opacity */
.uifw-cursor-prompt {
  padding: 5px;
  color: var(--iui-color-text);
  min-width: 100px;
  max-width: 340px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.uifw-cursor-prompt > .uifw-cursor-prompt-icon {
  padding-left: 3px;
  padding-right: 3px;
  width: 16px;
  height: 16px;
}
.uifw-cursor-prompt > .uifw-cursor-prompt-text {
  padding-left: 3px;
  padding-right: 3px;
}