.uifw-widgetPanels-toolbars {
  display: grid;
  height: 100%;
  width: 100%;
  padding: 0.75em;
  box-sizing: border-box;
  grid-template-columns: 1fr 1fr;
}
.uifw-widgetPanels-toolbars .nz-tools-widget {
  grid-column: 1;
}
.uifw-widgetPanels-toolbars .nz-tools-widget.nz-widget-navigationArea {
  grid-column: 2;
}