.uifw-accudraw-field-container {
  padding: 6px;
}
.uifw-accudraw-field-container .iui-input {
  padding: var(--iui-size-3xs) var(--iui-size-xs);
}

.uifw-accudraw-field-container-horizontal {
  max-width: 550px;
  display: flex;
  flex-direction: row;
}

.uifw-accudraw-field-container-vertical {
  max-width: 250px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  row-gap: var(--iui-size-2xs);
}