/** Toolbar Opacity */
.speed-timeline {
  position: relative;
  height: 25px;
}
.speed-timeline .railOuter {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.speed-timeline .railInner {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
  pointer-events: none;
  background: var(--buic-background-5);
}
.speed-timeline .scrubberHandle {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
  width: 12px;
  height: 12px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  background: var(--buic-foreground-primary);
}