/** Toolbar Opacity */
.nz-footer-snapMode-snap {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 8px;
  padding: 4px 8px;
  align-items: center;
  cursor: pointer;
  color: var(--iui-color-text);
  position: relative;
}
.nz-footer-snapMode-snap > .nz-icon {
  grid-row: 1;
  color: var(--iui-color-icon);
}
.nz-footer-snapMode-snap > .nz-label {
  grid-row: 1;
}
.nz-footer-snapMode-snap.nz-active {
  color: var(--iui-color-text-accent);
}
.nz-footer-snapMode-snap.nz-active:before {
  content: "";
  display: block;
  position: absolute;
  background-color: currentColor;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
}
.nz-footer-snapMode-snap:hover {
  color: var(--iui-color-text-accent-hover);
}
.nz-footer-snapMode-snap:focus {
  outline: none;
}