.uifw-menu-button {
  position: absolute;
  pointer-events: auto;
}
.uifw-menu-button .icon {
  line-height: 2;
}
.uifw-menu-button > .context-menu {
  top: calc(calc((22px - calc(var(--iui-size-s) * 1.4)) * 0.5) - 2px);
}