.uifw-view-overlay {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: calc(100% - calc(var(--iui-size-l) * 2) * 2);
  height: auto;
  position: absolute;
  bottom: var(--iui-size-s);
  left: calc(var(--iui-size-l) * 2);
  right: calc(var(--iui-size-l) * 2);
  pointer-events: none;
  z-index: 1000;
}

.uifw-animation-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 1000px;
  height: auto;
  background: var(--iui-color-background);
  border: 1px solid var(--iui-color-border);
  border-radius: var(--iui-border-radius-1);
  opacity: 0.9;
  transition: opacity var(--iui-duration-1) ease;
  pointer-events: auto;
}
.uifw-animation-overlay:hover {
  opacity: 1;
}