.property-item-line {
  display: grid;
  grid-template-columns: minmax(50%, auto) minmax(auto, 50%);
  align-items: center;
}
.property-item-line .property-badge-container {
  max-width: 100%;
  justify-self: end;
}
.property-item-line .property-badge-container .badge {
  max-width: 100%;
  display: inline-block;
}
.property-item-line .property-badge-container .badge .iui-badge,
.property-item-line .property-badge-container .badge .property-category-badge {
  max-width: 100%;
  vertical-align: middle;
  box-sizing: border-box;
  padding: 1px 6px;
  text-transform: none;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  direction: rtl;
}
.property-item-line .property-display-label {
  overflow: hidden;
  text-overflow: ellipsis;
}

.tooltip-content-header {
  text-align: left;
}

.tooltip-content-data {
  white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all;
  text-align: left;
}