@keyframes uifw-field-fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  50% {
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes uifw-field-fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.uifw-statusbar-field {
  visibility: visible;
  gap: var(--iui-size-xs);
}
.uifw-statusbar-field.uifw-action {
  cursor: pointer;
}
.uifw-statusbar-field.uifw-action:hover {
  color: var(--iui-color-text-accent-hover);
}
.uifw-statusbar-field.uifw-field-fade-in {
  animation: uifw-field-fadeIn 1s linear;
  opacity: 1;
  pointer-events: auto;
}
.uifw-statusbar-field.uifw-field-fade-out {
  animation: uifw-field-fadeOut 500ms linear;
  opacity: 0;
  pointer-events: none;
}
.uifw-statusbar-field > .nz-balloon-container {
  height: 100%;
  position: relative;
  display: grid;
  align-content: center;
}
.uifw-statusbar-field > .nz-balloon-container > .nz-balloon {
  background-color: var(--iui-color-text);
  text-align: center;
  cursor: pointer;
  min-width: 10px;
  min-height: 10px;
  border-radius: 50%;
  padding: 3px;
  filter: drop-shadow(0 1px var(--buic-foreground-body-reverse));
  position: relative;
  align-self: center;
}
.uifw-statusbar-field > .nz-balloon-container > .nz-balloon > .nz-arrow {
  position: absolute;
  width: 0;
  bottom: -3px;
  left: 0;
  display: block;
  border-width: 0;
  border-bottom-width: 10px;
  border-left-width: 10px;
  border-style: solid;
  border-color: transparent var(--iui-color-icon);
  transform: rotate(25deg);
}
.uifw-statusbar-field > .nz-balloon-container > .nz-balloon > .nz-content {
  position: relative;
  color: #fff;
  line-height: 10px;
  font-size: 11px;
}
.uifw-statusbar-field > .nz-balloon-container > .nz-dialog {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 50%;
}