.core-message-box-container {
  display: flex;
  color: var(--iui-color-text);
}
.core-message-box-container .core-message-box-icon {
  flex: 0 0;
  font-size: 32px;
  padding: 0 var(--iui-size-s) var(--iui-size-s) var(--iui-size-s);
  margin-top: 10px;
}
.core-message-box-container .core-message-box-content {
  display: flex;
  align-items: center;
  flex: 1 1;
}
.core-message-box-container .core-message-box-content a {
  color: var(--iui-color-text-accent);
  -webkit-tap-highlight-color: hsl(var(--iui-color-accent-hsl)/var(--iui-opacity-6));
  border-radius: var(--iui-border-radius-1);
  cursor: pointer;
  text-decoration: none;
}

.core-message-box-success {
  color: var(--iui-color-text-positive);
}

.core-message-box-information,
.core-message-box-question {
  color: var(--iui-color-text-informational);
}

.core-message-box-warning {
  color: var(--iui-color-text-warning);
}

.core-message-box-error,
.core-message-box-fatal {
  color: var(--iui-color-text-negative);
}

.notifymessage-icon {
  margin-right: 6px;
}