.nz-footer-messageCenter-tab {
  min-width: 40px;
  min-height: 38px;
  box-sizing: border-box;
  cursor: pointer;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: var(--iui-color-text);
}
.nz-footer-messageCenter-tab:not(.nz-active) {
  border-bottom: 1px solid var(--iui-color-border);
  background-color: var(--iui-color-background-backdrop);
}
.nz-footer-messageCenter-tab:hover:after {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  top: 2px;
  left: 0;
  background: var(--iui-color-text-accent);
  padding-left: 2px;
  padding-right: 2px;
  box-sizing: border-box;
  background-clip: content-box;
}
.nz-footer-messageCenter-tab.nz-active {
  color: var(--iui-color-text-accent);
}
.nz-footer-messageCenter-tab.nz-active:after {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  top: 2px;
  left: 0;
  background: var(--iui-color-text-accent);
  padding-left: 2px;
  padding-right: 2px;
  box-sizing: border-box;
  background-clip: content-box;
}
.nz-footer-messageCenter-tab:not(:first-child) {
  border-left: 1px solid var(--iui-color-border);
}
.nz-footer-messageCenter-tab:focus {
  outline: none;
}