.nz-popup-tooltip {
  position: absolute;
  background-color: var(--buic-background-tooltip);
  color: var(--iui-color-text);
  display: grid;
  grid-auto-flow: column;
  grid-gap: 5px;
  align-items: center;
  padding: 5px 10px;
  z-index: 18000;
}
.nz-popup-tooltip > .nz-content {
  white-space: nowrap;
}