.nz-widget-menuTab {
  display: flex;
  line-height: 2em;
  height: 2em;
  cursor: pointer;
  user-select: none;
  padding-right: 3em;
  padding-left: 0.5em;
}
.nz-widget-menuTab.nz-no-icon {
  padding-left: 1.25em;
}
.nz-widget-menuTab > .nz-icon {
  width: 2em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nz-widget-menuTab > .nz-badge {
  position: absolute;
  left: 0.25em;
}
.nz-widget-menuTab > span {
  max-width: 15em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.nz-widget-menuTab > .nz-checkmark {
  position: absolute;
  right: 0.75em;
  border-style: solid;
  border-color: var(--iui-color-border-accent);
  border-width: 0 0.15em 0.15em 0;
  width: 0.5em;
  height: 1em;
  transform: rotate(45deg);
}
.nz-widget-menuTab > .nz-checkmark.nz-hidden {
  display: none;
}
@media (hover: hover) {
  .nz-widget-menuTab:hover {
    color: var(--iui-color-text-accent);
  }
}