.nz-widget-contentContainer {
  flex: 1 1 auto;
  min-height: 0;
  min-width: 0;
  position: relative;
  display: flex;
}
.nz-widget-contentContainer > .nz-content {
  flex: 1;
  display: flex;
  min-width: 0;
}

.nz-widget-floatingWidget .nz-widget-contentContainer.nz-minimized {
  flex-basis: 0;
  overflow: hidden;
}