.nz-outline-panelOutline {
  background-color: rgba(140, 210, 255, var(--iui-opacity-3));
  position: absolute;
  transform: scale(1);
  transition: transform var(--iui-duration-1) ease-in;
  width: 100%;
  height: 100%;
}
.nz-outline-panelOutline.nz-hidden {
  transition: none;
}
.nz-outline-panelOutline.nz-left, .nz-outline-panelOutline.nz-right {
  grid-row: 2/3;
  height: 100%;
}
.nz-outline-panelOutline.nz-left {
  grid-column: 1;
  transform-origin: left;
}
.nz-outline-panelOutline.nz-left.nz-hidden {
  transform: scale(0, 1);
}
.nz-outline-panelOutline.nz-right {
  grid-column: 3;
  right: 0;
  transform-origin: right;
}
.nz-outline-panelOutline.nz-right.nz-hidden {
  transform: scale(0, 1);
}
.nz-outline-panelOutline.nz-top, .nz-outline-panelOutline.nz-bottom {
  grid-column: 2/3;
  width: 100%;
}
.nz-outline-panelOutline.nz-top.nz-span, .nz-outline-panelOutline.nz-bottom.nz-span {
  grid-column: 1/4;
}
.nz-outline-panelOutline.nz-top {
  grid-row: 1;
  transform-origin: top;
}
.nz-outline-panelOutline.nz-top.nz-hidden {
  transform: scale(1, 0);
}
.nz-outline-panelOutline.nz-bottom {
  grid-row: 3;
  bottom: 0;
  transform-origin: bottom;
}
.nz-outline-panelOutline.nz-bottom.nz-hidden {
  transform: scale(1, 0);
}