.uifw-sheet-navigation {
  height: 96px;
  width: 96px;
  box-sizing: border-box;
  border-color: var(--buic-background-widget-stroke);
  border-width: 1px;
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  color: var(--iui-color-text);
  background: var(--buic-background-window);
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.uifw-sheet-navigation > .sheet-title {
  font-size: 12px;
  font-weight: bold;
}
.uifw-sheet-navigation > .sheet-name {
  cursor: pointer;
  font-size: 11px;
  z-index: 1;
  height: 46px;
  overflow: hidden;
  margin: 0 2px;
}
.uifw-sheet-navigation > .sheet-name:focus {
  outline: none;
}
.uifw-sheet-navigation > .sheet-container {
  flex: 1 1 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 11px;
}
.uifw-sheet-navigation > .sheet-container > * {
  text-align: center;
}