/* Breakpoints */
.uifw-statusbar-labelIndicator.uifw-reversed {
  flex-direction: row-reverse;
}
.uifw-statusbar-labelIndicator > .uifw-label {
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
@media (max-width: 1099px) {
  .uifw-statusbar-labelIndicator > .uifw-label {
    display: none;
  }
}
.uifw-statusbar-labelIndicator > .uifw-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  width: 16px;
