/** Toolbar Opacity */
.core-lb {
  display: flex;
  align-items: center;
}
.core-lb > .lb-container {
  background-color: var(--buic-background-5);
  flex: 1;
}
.core-lb > .lb-container > .fill {
  height: 100%;
  background-color: var(--buic-foreground-primary);
  transition: width 0.3s linear;
}
.core-lb > .percent {
  flex: initial;
  opacity: 0.85;
  width: 3em;
  font-size: 0.9em;
  margin-left: 0.3em;
}