#uifw-configurableui-wrapper {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-size: var(--iui-font-size-1);
}

#uifw-frontstage-composer {
  width: 100%;
  height: 100%;
}

.uifw-unselectable {
  user-select: none;
}

.uifw-centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.uifw-dragdrop-layer {
  cursor: grabbing;
  position: fixed;
  pointer-events: none;
  z-index: 20000;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

.uifw-element-tooltip-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 18000;
}
.uifw-element-tooltip-container > .uifw-element-tooltip {
  width: max-content;
  font-size: var(--iui-font-size-1);
}
.uifw-element-tooltip-container > .nz-popup-tooltip {
  border-color: var(--iui-color-border);
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  border-width: 1px;
}

.uifw-app-button-small {
  margin-bottom: 6px;
}