.uifw-sheet-card {
  position: relative;
  background-color: #f1f1f1;
  width: 200px;
  height: 250px;
  margin: 10px;
  padding: 10px;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  border-color: #f1f1f1;
  border-width: 8px;
  border-radius: 12px;
  border-style: solid;
  box-sizing: border-box;
  user-select: none;
}
.uifw-sheet-card:hover {
  cursor: pointer;
}
.uifw-sheet-card:focus {
  outline: none;
}
.uifw-sheet-card .sheet-image-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.uifw-sheet-card .sheet-image-container .icon {
  width: 50px;
  height: 50px;
  font-size: 50px;
}
.uifw-sheet-card .sheet-index {
  position: absolute;
  width: 100%;
  bottom: 10px;
  left: 0;
}
.uifw-sheet-card.is-active {
  border-color: #008be1;
}
.uifw-sheet-card.is-pressed {
  border-color: #008be1;
}
.uifw-sheet-card:not(.is-active):not(.is-pressed):hover {
  border-color: #a6ddff;
}