.uifw-card {
  background-color: var(--buic-background-card);
  z-index: 3000;
}
.uifw-card .uifw-card-content {
  padding: 6px 10px;
}
.uifw-card .uifw-card-separator {
  height: 2px;
  margin-bottom: 1px;
  background: rgba(var(--buic-foreground-body-reverse-rgb), 0.45);
}

.uifw-card-gap {
  height: 4px;
}