/* Breakpoints */
.nz-widget-tab {
  user-select: none;
  white-space: nowrap;
  display: flex;
  gap: 6px;
  align-items: center;
  cursor: pointer;
  padding: 4px 1em 0;
  position: relative;
  border: 0 solid var(--iui-color-border);
  max-width: 9em;
  background: var(--iui-color-background-backdrop);
}
.nz-widget-tab > span {
  font-size: 0.9em;
  text-overflow: ellipsis;
  overflow: hidden;
}
.nz-widget-tab > .nz-badge {
  position: absolute;
  top: 0;
  left: 0;
}
.nz-widget-tab:not(.nz-minimized).nz-active {
  background: var(--iui-color-background);
  color: var(--iui-color-text-accent);
  padding-bottom: 1px;
  margin-bottom: -1px;
}
.nz-widget-tab:not(.nz-minimized).nz-active:before {
  content: "";
  position: absolute;
  background: currentColor;
  left: 0.25em;
  top: 1px;
  border-radius: 0.2em;
  width: calc(100% - 0.5em);
  height: 0.15em;
}
.nz-widget-tab:not(.nz-minimized).nz-active.nz-last {
  border-right-width: 1px;
}
.nz-widget-tab:not(.nz-active), .nz-widget-tab.nz-minimized {
  color: var(--iui-color-text-muted);
  border-right-width: 1px;
}
.nz-widget-tab:not(.nz-minimized).nz-first-inactive {
  border-left-width: 1px;
}
.nz-widget-tab.nz-first {
  border-top-left-radius: 2px;
}
.nz-widget-tab:hover {
  background: var(--iui-color-background);
}
.nz-widget-tab:hover:not(.nz-active) {
  color: var(--iui-color-text);
}