/** Toolbar Opacity */
.nz-toolbar-button-icon {
  font-size: 36px;
}
.nz-toolbar-button-icon > .nz-icon {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nz-toolbar-button-icon > .nz-icon > * {
  width: 36px;
  height: 36px;
  color: var(--iui-color-icon);
  filter: drop-shadow(0 1px 0 var(--buic-foreground-body-reverse));
  display: inline-block;
}
.nz-toolbar-button-icon > .nz-icon > .icon.core-svg-icon svg {
  width: 36px;
  height: 36px;
}
.nz-toolbar-button-icon.nz-toolbar-button-icon-small {
  font-size: 24px;
}
.nz-toolbar-button-icon.nz-toolbar-button-icon-small > .nz-icon > * {
  width: 24px;
  height: 24px;
}
.nz-toolbar-button-icon:hover > .nz-icon > * {
  color: var(--iui-color-icon-hover);
}