/** Toolbar Opacity */
.nz-widget-pinToggle {
  position: relative;
  padding: 0 0.5em 0 0;
  border: 0;
  background: none;
  cursor: pointer;
  outline: none;
  color: var(--iui-color-text-muted);
}
@media (hover: hover) {
  .nz-widget-pinToggle:hover {
    color: var(--iui-color-text-hover);
  }
}