.nz-widgetPanels-expander {
  position: absolute;
}
.nz-widgetPanels-expander.nz-left {
  width: 1em;
  height: 100%;
  top: 0;
  left: 0;
}
.nz-widgetPanels-expander.nz-right {
  width: 1em;
  height: 100%;
  top: 0;
  right: 0;
}
.nz-widgetPanels-expander.nz-top {
  width: 100%;
  height: 1em;
  top: 0;
  left: 0;
}
.nz-widgetPanels-expander.nz-bottom {
  width: 100%;
  height: 1em;
  bottom: 0;
  left: 0;
}