.nz-footer-footer {
  position: relative;
}
.nz-footer-footer > div {
  background-color: var(--iui-color-background);
  border-width: 0;
  border-top-width: 1px;
  border-color: var(--iui-color-border);
  border-style: solid;
  box-sizing: border-box;
  bottom: 0;
  width: 100%;
  pointer-events: auto;
}
.nz-footer-footer > div > .nz-messages {
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%) translateY(-100%);
  z-index: 17000;
}
.nz-footer-footer > div > .nz-indicators {
  display: flex;
  flex-direction: row;
  height: 40px;
}
.nz-footer-footer.nz-safe-area-left > div {
  /* iOS 11 */
  padding-left: constant(safe-area-inset-left);
  /* iOS 11.2+ */
  padding-left: env(safe-area-inset-left);
}
.nz-footer-footer.nz-safe-area-right > div {
  /* iOS 11 */
  padding-right: constant(safe-area-inset-right);
  /* iOS 11.2+ */
  padding-right: env(safe-area-inset-right);
}
.nz-footer-footer.nz-safe-area-bottom > div {
  /* iOS 11 */
  padding-bottom: constant(safe-area-inset-bottom);
  /* iOS 11.2+ */
  padding-bottom: env(safe-area-inset-bottom);
}