.components-enumbuttongroup-editor {
  font-family: var(--iui-font-sans);
  font-size: var(--iui-font-size-1);
  background: var(--iui-color-background);
  width: fit-content;
  border-color: var(--iui-color-border-foreground);
  border-width: 1px;
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  overflow: hidden;
  display: flex;
  vertical-align: bottom;
  box-sizing: border-box;
  height: 32px;
}
.components-enumbuttongroup-editor > * {
  display: inline-block;
  height: 100%;
  width: 32px;
}
.components-enumbuttongroup-editor > *:not(:first-child) {
  margin-left: 1px;
}
.components-enumbuttongroup-editor > *:not(:first-child):before {
  content: "";
  background-color: var(--iui-color-border-foreground);
  display: block;
  position: absolute;
  pointer-events: none;
  width: 1px;
  height: 100%;
  left: -1px;
  top: 0;
}

.components-enumbuttongroup-button {
  border-width: 0;
  background-color: var(--iui-color-background);
  background-origin: border-box;
  border-color: var(--iui-color-border);
  border-style: solid;
  cursor: pointer;
  transition-property: background-color, color;
  transition-duration: 500ms;
  transition-timing-function: ease;
  position: relative;
  height: 32px;
  width: 32px;
}
.components-enumbuttongroup-button > i {
  font-size: 16px;
  width: 100%;
  height: 100%;
  display: grid;
  align-content: center;
  justify-content: center;
  margin: 0;
  color: var(--iui-color-icon);
  transition: all 500ms ease;
}
.components-enumbuttongroup-button > i img {
  width: 100%;
  height: 100%;
}
.components-enumbuttongroup-button:hover > i {
  color: var(--iui-color-text-accent-hover);
  transition-duration: 150ms;
}
.components-enumbuttongroup-button.nz-is-active {
  background: var(--iui-color-background);
}
.components-enumbuttongroup-button.nz-is-active:not(.nz-is-disabled) {
  transition-duration: 0s;
}
.components-enumbuttongroup-button.nz-is-active:not(.nz-is-disabled) > i {
  color: var(--iui-color-icon-accent);
  transition-duration: 150ms;
  transition-timing-function: cubic-bezier(1, 0.1, 1, 0.5);
}
.components-enumbuttongroup-button.nz-is-active:not(.nz-is-disabled):after {
  content: "";
  display: block;
  position: absolute;
  background: var(--iui-color-border-accent);
  left: 3px;
  top: 5px;
  border-radius: 2px;
  width: 2px;
  height: 22px;
}
.components-enumbuttongroup-button.nz-is-disabled {
  pointer-events: none;
  background-color: var(--iui-color-background-disabled);
  border-color: var(--iui-color-border-disabled);
  cursor: not-allowed;
}
.components-enumbuttongroup-button.nz-is-disabled > i {
  color: var(--iui-color-icon-disabled);
}
.components-enumbuttongroup-button.nz-is-disabled:hover {
  cursor: auto;
}
.components-enumbuttongroup-button.nz-is-disabled:active {
  pointer-events: none;
}