.components-toolbar-button-item.components-ellipsis-icon {
  position: relative;
}
.components-toolbar-button-item.components-ellipsis-icon > .components-icon .components-ellipsis {
  position: relative;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: currentColor;
}
.components-toolbar-button-item.components-ellipsis-icon > .components-icon .components-ellipsis:before {
  content: "";
  position: absolute;
  left: -8px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: currentColor;
}
.components-toolbar-button-item.components-ellipsis-icon > .components-icon .components-ellipsis:after {
  content: "";
  position: absolute;
  left: 8px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: currentColor;
}

.components-toolbar-overflow_popup {
  border: none;
  background: none;
  z-index: 9000;
  transition: visibility var(--iui-duration-2) ease, opacity var(--iui-duration-2) ease;
}
.components-toolbar-overflow_popup.nz-hidden {
  opacity: 0;
  visibility: hidden;
}