.presentation-components-node .presentation-components-node-action-buttons {
  visibility: hidden;
  position: sticky;
  background-color: var(--iui-color-background);
  right: 0;
  display: flex;
}
.presentation-components-node .presentation-components-node-action-buttons .iui-button {
  height: 24px;
  width: 24px;
}
.presentation-components-node:hover > .presentation-components-node-action-buttons {
  visibility: visible;
  background-color: inherit;
}
.presentation-components-node .presentation-components-node-action-buttons.filtered {
  visibility: visible;
}
.presentation-components-node.is-selected > .presentation-components-node-action-buttons.filtered {
  background-color: inherit;
}