.components-property-label-renderer {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-top: auto;
  margin-bottom: auto;
}

.components-property-label-renderer-colon {
  margin: auto 3px auto 0px;
}

.components-primitive-property-label-renderer {
  position: relative;
  display: flex;
}
.components-primitive-property-label-renderer .components-label-popup {
  background: var(--buic-background-window);
  width: max-content;
  padding: 5px;
}

.components-property-record--vertical .components-nonprimitive-property-label-renderer {
  margin-bottom: 4px;
}

.components-nonprimitive-property-label-renderer {
  position: relative;
  display: flex;
  align-items: center;
  border: none;
  background: transparent;
  cursor: pointer;
  color: var(--iui-color-text);
  font-size: 14px;
  height: 100%;
  width: 100%;
  transition: transform var(--iui-duration-1) ease-in-out, -webkit-transform var(--iui-duration-1) ease-in-out;
}
.components-nonprimitive-property-label-renderer .components-label-popup {
  background: var(--buic-background-window);
  width: max-content;
  padding: 5px;
}
.components-nonprimitive-property-label-renderer > .components-property-label-renderer {
  flex: 1;
  padding-left: var(--iui-size-2xs);
}
.components-nonprimitive-property-label-renderer > div {
  -webkit-transition: -webkit-transform var(--iui-duration-1) ease-in-out;
  -ms-transition: -ms-transform var(--iui-duration-1) ease-in-out;
  transition: transform var(--iui-duration-1) ease-in-out;
}
.components-nonprimitive-property-label-renderer > div svg {
  width: var(--iui-size-s);
  height: var(--iui-size-s);
  fill: var(--iui-color-icon);
  display: block;
}
.components-nonprimitive-property-label-renderer > div.components-expanded {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}
.components-nonprimitive-property-label-renderer:hover {
  color: var(--iui-color-text-hover);
}