.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list.enlarge .without-expander .visibility-tree-checkbox-container + * {
  margin-left: var(--enlarged-node-expander-additional-padding);
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list.enlarge .visibility-tree-checkbox-container {
  height: var(--enlarged-node-height);
  width: var(--enlarged-node-height);
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list.enlarge .visibility-tree-checkbox-container .visibility-tree-checkbox {
  --iui-checkbox-target-size: var(--enlarged-node-height);
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node.with-checkbox > .contents {
  padding-left: calc(var(--iui-size-3xs) / 2);
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node.disable-expander > .contents {
  /* stylelint-disable-next-line selector-class-pattern */
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node.disable-expander > .contents > .core-tree-expansionToggle {
  display: none;
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node .visibility-tree-checkbox-container {
  height: var(--iui-size-l);
  width: var(--iui-size-l);
  position: sticky;
  order: -1;
  left: 0;
  background-color: var(--iui-color-background);
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node .visibility-tree-checkbox-container .visibility-tree-checkbox:disabled {
  background-color: transparent;
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node .visibility-tree-checkbox-container .visibility-tree-checkbox:hover {
  background-color: transparent;
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node.is-selected > .contents > .visibility-tree-checkbox-container {
  background-color: var(--iui-color-background-accent-muted);
}
.tree-widget-visibility-tree-base .tree-widget-tree-nodes-list .core-tree-node:not(.is-selected) > .contents:hover > .visibility-tree-checkbox-container {
  background-color: var(--iui-color-background-hover);
}