/** Toolbar Opacity */
.nz-widget-dock {
  position: relative;
  padding: 0 0.5em;
  border: 0;
  background: none;
  cursor: pointer;
  outline: none;
}
.nz-widget-dock > i {
  width: 1.2em;
  height: 1em;
  border-width: 0.15em;
  border-style: solid;
  border-color: var(--buic-foreground-muted);
  display: block;
}
.nz-widget-dock > i:after {
  content: "";
  background: var(--buic-foreground-muted);
  height: 33%;
  display: block;
}
@media (hover: hover) {
  .nz-widget-dock:hover {
    border-color: var(--buic-foreground-activehover);
  }
  .nz-widget-dock:hover > i:after {
    background: var(--buic-foreground-activehover);
  }
}