.tree-widget-selectable-tree {
  padding: var(--iui-size-xs);
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.tree-widget-selectable-tree .components-selectable-content {
  flex: 1;
  overflow: hidden;
}
.tree-widget-selectable-tree .components-selectable-content-header {
  padding-bottom: var(--iui-size-2xs);
}
.tree-widget-selectable-tree .components-selectable-content-wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
}