.uifw-widgetPanels-frontstage {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.uifw-widgetPanels-frontstage > .uifw-widgetPanels {
  flex: 1 1 auto;
  min-height: 0;
}
.uifw-widgetPanels-frontstage > .uifw-widgetPanels > .nz-widgetPanels-content {
  position: relative;
  pointer-events: none;
}
.uifw-widgetPanels-frontstage .preview-contentAlwaysMaxSize-toolSettingsDocked .nz-widgetPanels-panel.nz-top,
.uifw-widgetPanels-frontstage .preview-contentAlwaysMaxSize-toolSettingsDocked .nz-widgetPanels-panel.nz-left,
.uifw-widgetPanels-frontstage .preview-contentAlwaysMaxSize-toolSettingsDocked .nz-widgetPanels-panel.nz-right,
.uifw-widgetPanels-frontstage .preview-contentAlwaysMaxSize-toolSettingsDocked .nz-widgetPanels-centerContent {
  margin-top: calc(2.5em + 6.8px);
}
.uifw-widgetPanels-frontstage .preview-contentAlwaysMaxSize-topPanelActive .nz-widgetPanels-panel.nz-left,
.uifw-widgetPanels-frontstage .preview-contentAlwaysMaxSize-topPanelActive .nz-widgetPanels-panel.nz-right,
.uifw-widgetPanels-frontstage .preview-contentAlwaysMaxSize-topPanelActive .nz-widgetPanels-centerContent {
  margin-top: 0;
}