.nz-target-panelTargets {
  position: absolute;
  justify-content: center;
}
.nz-target-panelTargets.nz-wide {
  gap: 6em;
}
.nz-target-panelTargets.nz-left, .nz-target-panelTargets.nz-right {
  grid-row: 2/3;
  height: 100%;
}
.nz-target-panelTargets.nz-left {
  grid-column: 1;
  left: 0.75em;
}
.nz-target-panelTargets.nz-right {
  grid-column: 3;
  right: 0.75em;
}
.nz-target-panelTargets.nz-top, .nz-target-panelTargets.nz-bottom {
  grid-column: 2/3;
  width: 100%;
}
.nz-target-panelTargets.nz-top.nz-span, .nz-target-panelTargets.nz-bottom.nz-span {
  grid-column: 1/4;
}
.nz-target-panelTargets.nz-top {
  grid-row: 1;
  top: 0.75em;
}
.nz-target-panelTargets.nz-bottom {
  grid-row: 3;
  bottom: 0.75em;
}