/* Breakpoints */
#uifw-contentlayout-div {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  pointer-events: auto;
}
#uifw-contentlayout-div .Pane2 {
  height: 100%;
}
#uifw-contentlayout-div .Resizer {
  background-color: var(--iui-color-border);
  box-sizing: border-box;
  background-clip: padding-box;
  margin: 0;
  transition: all var(--iui-duration-1) ease;
}
#uifw-contentlayout-div .Resizer.horizontal {
  height: 1px;
  min-height: 1px;
  width: 100%;
  cursor: ns-resize;
}
#uifw-contentlayout-div .Resizer.horizontal > .nz-resizer-grip {
  height: 5px;
  width: 100%;
  top: -2px;
}
#uifw-contentlayout-div .Resizer.horizontal > .nz-resizer-grip:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 11px;
}
#uifw-contentlayout-div .Resizer.vertical {
  width: 1px;
  min-width: 1px;
  height: 100%;
  cursor: ew-resize;
}
#uifw-contentlayout-div .Resizer.vertical > .nz-resizer-grip {
  height: 100%;
  width: 5px;
  left: -2px;
}
#uifw-contentlayout-div .Resizer.vertical > .nz-resizer-grip:before {
  content: "";
  position: absolute;
  width: 11px;
  height: 100%;
}
#uifw-contentlayout-div .Resizer.horizontal > .nz-resizer-grip, #uifw-contentlayout-div .Resizer.vertical > .nz-resizer-grip {
  background-color: transparent;
  align-items: center;
  justify-content: center;
  position: relative;
  display: flex;
  z-index: 1;
  transition: background-color var(--iui-duration-1) ease;
}
#uifw-contentlayout-div .Resizer.horizontal > .nz-resizer-grip:hover, #uifw-contentlayout-div .Resizer.vertical > .nz-resizer-grip:hover {
  background-color: var(--iui-color-border-accent);
}
#uifw-contentlayout-div .Resizer.disabled {
  cursor: not-allowed;
}
#uifw-contentlayout-div .Resizer.disabled > .nz-resizer-grip:hover {
  border-color: transparent;
}
#uifw-contentlayout-div .uifw-contentlayout-full-size {
  height: 100%;
  width: 100%;
}

div.uifw-contentlayout-full-size div.SplitPane div.Pane {
  overflow: hidden;
}

.uifw-contentlayout-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.uifw-contentlayout-wrapper .uifw-contentlayout-overlay-div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-style: solid;
  border-width: 4px 0 0 0;
  box-sizing: border-box;
  pointer-events: none;
}
.uifw-contentlayout-wrapper .uifw-contentlayout-overlay-div.uifw-contentlayout-overlay-active {
  border-color: var(--iui-color-border-warning);
}
.uifw-contentlayout-wrapper .uifw-contentlayout-overlay-div.uifw-contentlayout-overlay-inactive {
  border-color: transparent;
}