.nz-outline-sectionOutline {
  background-color: rgba(140, 210, 255, var(--iui-opacity-3));
}
.nz-outline-sectionOutline.nz-horizontal {
  transition: width var(--iui-duration-1) ease-in;
  height: 100%;
  width: 50%;
  top: 0;
}
.nz-outline-sectionOutline.nz-horizontal.nz-0 {
  left: 0;
}
.nz-outline-sectionOutline.nz-horizontal.nz-1 {
  right: 0;
}
.nz-outline-sectionOutline.nz-horizontal.nz-hidden {
  width: 0;
}
.nz-outline-sectionOutline.nz-vertical {
  transition: height var(--iui-duration-1) ease-in;
  width: 100%;
  height: 50%;
  left: 0;
}
.nz-outline-sectionOutline.nz-vertical.nz-0 {
  top: 0;
}
.nz-outline-sectionOutline.nz-vertical.nz-1 {
  bottom: 0;
}
.nz-outline-sectionOutline.nz-vertical.nz-hidden {
  height: 0;
}