/** Toolbar Opacity */
.nz-toolSettings-overflow {
  height: 100%;
  width: 3em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  cursor: pointer;
  flex-shrink: 0;
}
.nz-toolSettings-overflow:hover {
  background: var(--buic-background-2);
}
.nz-toolSettings-overflow:focus {
  outline: none;
}
.nz-toolSettings-overflow.nz-hidden {
  visibility: hidden;
}