.property-grid-react-element-list {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.property-grid-react-element-list .property-grid-react-element-list-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.property-grid-react-element-list .property-grid-react-element-list-container {
  overflow: auto;
}
.property-grid-react-element-list .property-grid-react-element-list-container li:nth-child(odd) {
  background-color: var(--iui-color-background-zebra);
}