.uifw-cursorpopup {
  border-color: var(--iui-color-border);
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  border-width: 1px;
  background-color: hsl(var(--iui-color-background-hsl)/var(--iui-opacity-2));
  position: fixed;
  height: fit-content;
  width: fit-content;
  transition: opacity 500ms;
}
.uifw-cursorpopup > .uifw-cursorpopup-title {
  min-height: var(--iui-size-l);
  opacity: var(--iui-opacity-2);
}
.uifw-cursorpopup > .uifw-cursorpopup-content {
  padding: var(--iui-size-2xs);
}
.uifw-cursorpopup.uifw-cursorpopup-fadeOut {
  opacity: 0;
}