/** Toolbar Opacity */
.nz-footer-toolAssistance-indicator > .nz-indicator {
  height: 32px;
  min-width: 20px;
  position: relative;
  padding-top: 1px;
  padding-right: 9px;
  padding-bottom: 1px;
  padding-left: 2px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.nz-footer-toolAssistance-indicator > .nz-indicator > .nz-icons {
  font-size: 16px;
  display: flex;
  align-items: center;
}
.nz-footer-toolAssistance-indicator > .nz-indicator > .nz-content {
  margin-left: 8px;
  font-size: 14px;
  line-height: 1;
  /* Medium devices */
  /* Small devices */
}
@media (max-width: 799px) {
  .nz-footer-toolAssistance-indicator > .nz-indicator > .nz-content {
    font-size: 12px;
  }
}
@media (max-width: 599px) {
  .nz-footer-toolAssistance-indicator > .nz-indicator > .nz-content {
    font-size: 12px;
  }
}
.nz-footer-toolAssistance-indicator > .nz-indicator > .nz-triangle {
  position: absolute;
  right: 2px;
  bottom: 2px;
  filter: drop-shadow(0 1px var(--buic-foreground-body-reverse));
  border-width: 0 0 5px 5px;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent transparent var(--iui-color-icon) transparent;
}
.nz-footer-toolAssistance-indicator > .nz-indicator:hover {
  color: var(--iui-color-text-accent-hover);
}
.nz-footer-toolAssistance-indicator > .nz-indicator:hover > .nz-triangle {
  border-width: 0 0 5px 5px;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent transparent var(--iui-color-text-accent-hover) transparent;
}
.nz-footer-toolAssistance-indicator > .nz-indicator:focus {
  outline: none;
}