/** Toolbar Opacity */
.nz-toolbar-button-button {
  box-sizing: border-box;
  width: 60px;
  height: 60px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  background: var(--iui-color-background);
  border-radius: var(--iui-border-radius-1);
  border-width: 1px;
  border-color: var(--iui-color-border-foreground);
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  pointer-events: auto;
}
.nz-toolbar-button-button.nz-toolbar-button-icon-small {
  width: 42px;
  height: 42px;
}
.nz-toolbar-button-button > .nz-gradient {
  background: linear-gradient(to bottom, var(--iui-color-background), var(--iui-color-background-zebra));
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.nz-toolbar-button-button:focus {
  outline: none;
}

.nz-toolbar-button-background:hover {
  background-color: var(--iui-color-background-hover);
}